.bookmark_list {

  .bookmark_list_area {
    margin: 10px;
    padding: 10px;
    min-height:500px;
    background: #C9E7D5;
    border-radius: 10px;
    overflow: auto;
  }
  
  .currenct_directory{
    cursor: default;
    margin: 10px 20px 30px 20px;
    padding-left: 10px;
    text-align: left;
    height: 25px;
    background: #ffff;

    img {
      cursor: pointer;
      vertical-align:middle;
      margin-right: 10px;
    }
  }
}

