.ogType {
  float: left;
  width:45%;
  margin: 0 0 10px 20px;
  cursor: pointer;
  
  .image {
    width:100%;
    background: #C4C4C4;
    border-radius: 10px 10px 0px 0px;

    img {
      width:100%;
      border-radius: 10px 10px 0px 0px;
    }
  }

  .text {
    background: #FFFFFF;
    border-radius: 0px 0px 10px 10px;
    text-align: left;
    min-height: 70px;
    
    .title {
      margin:0px 10px 10px 10px;
      font-family: Roboto;
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;

      color: #000000;
    }

    .desc {
      margin:10px;
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      font-size: 10px;
      line-height:15px;

      color: #000000;
    }
  }
}